import React, { Component } from 'react';
import { Button, Notification } from 'element-react';
import './index.scss';
import Codemirror from 'react-codemirror';
import { connect } from 'react-redux';
import { fromJS, is } from 'immutable';

class Xnotification extends Component {

	componentDidMount() {
	}

	shouldComponentUpdate(nextProps, nextState) {
		return !is(fromJS(this.props), fromJS(nextProps)) || !is(fromJS(this.state), fromJS(nextState))
	}
	
	open = () => {
		Notification({
			title: '标题名称',
			message: '这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案这是提示文案'
		});
	}

	open2 = () => {
		Notification({
			title: '提示',
			message: '这是一条不会自动关闭的消息',
			duration: 0
		});
	}

	render() {
		const { defaultOptions } = this.props.configs

		return (
			<div id="x-time-picker">
				<h3>Notification 通知</h3>
				<div className="mt-20 mb-20">
					<Button plain={true} onClick={this.open}>可自动关闭</Button>
					<Button plain={true} onClick={this.open2}>不会自动关闭</Button>
				</div>
				<Codemirror value={
					`<Button plain={true} onClick={this.open}>可自动关闭</Button>
<Button plain={true} onClick={this.open2}>不会自动关闭</Button>`
				} options={defaultOptions} />

			</div>
		);
	}
}

export default connect(state => ({
	configs: state.configs
}), {

	})(Xnotification)
